




























































import ut from '../../utils/index.js';
export default {
	data() {
		return {
			name:'',
			list:[],
			static:''
		}
	},
	onLoad() {
		this.static=ut.static;
	},
	methods: {
		req_goodslist(){
			ut.request({
				data: {
					name:this.name
				},
				url: "class/service/list"
			}).then(data=>{
				this.list=data;
				if(!this.list.length){
					ut.totast('暂无相关服务')
				}
			})
		},
		go_home_serverinf(_id,name){
			wx.navigateTo({
				url: `../home/serverinf?_id=${_id}&title=${name}`
			})
		}
	}
}
